@import "variables.scss";

.logo-container{
  &--footer {
    padding: 0 15px;
  }
}

.title {
  border-bottom: 4px solid $color-pink;
  font-weight: 400;

  &--hide {
    display: none;
  }

  &--price, &--advantages {
    margin: 0 -15px;
  }
}

.contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  
  &--header {
    max-width: 300px;
    width: 100%;
    @media (max-width: 930px) {
      max-width: 180px;
    }
  }

  &--footer {
    max-width: 230px;
    width: 100%;
    padding: 0 20px;

    a {
      color: #ffffff;
    }
  }

  a {
    font-size: 14px;
  }
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;

  &__burger {
    display: none;
  }

  &--header {
    .menu__item{
      padding-right: 30px;

      a {
        border-bottom: 2px solid transparent;
        transition: all 0.5s ease;

        &:hover {
          border-color: #000;
        }
      }

      span {
        font-size: 14px;
      }
    }
  }

  &--footer {
    .menu__item{
      padding-right: 20px;

      a {
        border-bottom: 1px solid transparent;
        transition: all 0.5s ease;

        &:hover {
          border-color:#fff;
        }
      }

      span {
        font-size: 10px;
        color: #ffffff;
      }
    }
  }

  &__item {
    list-style: none;

    &:last-child {
      padding-right: 0px;
    }
  }
}

.btn {
  display: block;
  border: 1px solid;
  border-radius: 999px;
  letter-spacing: 0.45px;
  transition: all 0.3s ease;
  padding: 20px 30px;

  >span {
    transition: all 0.3s ease;
    font-size: 14px;
  }

  &-container {
    &--hero-description {
      margin-top: 45px;
    }

    &--hero-form, &--price-calc {
      margin-top: 35px;
      align-self: center;
    }

    &--ring-past {
      margin-top: 30px;
    }
  }

  &--pink:hover {
    cursor: pointer;
    background-color: #fff;

    span {
      color: $color-pink;
    }
  }

  &--white:hover {
    cursor: pointer;
    background-color: $color-pink;

    span {
      color: #fff;
    }
  }

  &--hero-description {
    padding: 20px 64px;
    max-width: 190px;
    color: #ffffff;
    border-color: $color-pink;
    background-color: $color-pink;
  }

  &--hero-form {
    max-width: 190px;
    padding: 19px 50px;
    color: #ffffff;
    border-color: $color-pink;
    background-color: $color-pink;
  }

  &--ring-past {
    max-width: 220px;
    color: #000000;
    border-color: #ffffff;
    background-color: #ffffff;
  }

  &--price-calc {
    max-width: 220px;
    color: #000000;
    border-color: #ffffff;
    background-color: #ffffff;
    padding: 18px 60px;
  }
}